    display: flex;
    align-items: center;
    justify-content: center;
  }
  .cards li img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; /* Center the image */
    border-radius: 0.8rem;
  }
}
@media (max-width: 480px) {
  .gallery .cards {
    height: 70vh; /* Adjust as necessary */
  }
