  transition: all 0.3s ease-in-out;
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

.slick-prev:hover img,
.slick-next:hover img {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.next-slick-img {
  position: absolute;
  right: 0;
  top: 50%;
  width: 275px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  border-radius: 15px;
}

.next-slick-img img {
  width: 275px;
  border-radius: 15px;
}

.next-slick-img::after {
  content: "";
  width: 235px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border: 1px solid #676663;
  border-radius: 15px;
  margin: 20px;
}

